.choose-country {
  margin: 0 10px;
  padding-left: 4px;
  text-align: center;

  @include viewport--md {
    padding-left: 0;
  }
}

.choose-country__text {
  font-family: $font-family-inter;
  font-size: 17px;
  font-weight: 400;
  line-height: 22px;
  text-align: center;
  color: $color-default-white;
}

.choose-country__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  list-style: none;
  margin: 0;
  padding: 0;

  @include viewport--md {
    padding-left: 4px;
  }
}

.choose-country__list li {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 16px;
  margin-right: 50px;

  @include viewport--md {
    margin-top: 16px;
    margin-right: 26px;
  }
}

.choose-country__list li:last-child {
  margin-right: 0;
}

.choose-country__name {
  display: block;
  font-family: $font-family-inter;
  font-size: 18px;
  font-weight: 700;
  line-height: 21px;
  text-align: center;
  color: $color-default-white;
}

.choose-country__svg {
  margin-bottom: 5px;
  width: 57px;
  height: 57px;
  line-height: 0;
}

.choose-country__svg svg {
  position: relative;
  z-index: -1;
}

.choose-country__link {
  outline: none;
}

.choose-country__link .choose-country__svg {
  border-radius: 50%;
  transition: box-shadow 0.2s;
}

.choose-country__link.choose-country__link--korea:hover .choose-country__svg {
  box-shadow: 0 0 0 5px rgba(28, 84, 209, 0.5);
}

.choose-country__link.choose-country__link--japan:hover .choose-country__svg {
  box-shadow: 0 0 0 5px rgba(226, 53, 40, 0.5);
}

.choose-country__link--active {
  position: relative;

  &::after {
    content: "";
    position: absolute;
    bottom: -6px;
    left: 50%;
    transform: translateX(-50%);
    width: 56px;
    height: 3px;
    background-color: $color-default-white;
  }
}

.choose-country__link--active.choose-country__link--korea .choose-country__svg,
.choose-country__link--active.choose-country__link--korea:hover .choose-country__svg {
  box-shadow: inset 0 0 0 3px $color-cerulean-blue;
}

.choose-country__link--active.choose-country__link--japan .choose-country__svg,
.choose-country__link--active.choose-country__link--japan:hover .choose-country__svg {
  box-shadow: inset 0 0 0 3px $color-cinnabar;
}
